import { CollectionState, useOptimisticActions } from '@wix/patterns';
import { useEffect } from 'react';
import { useOptimisticActionsContext } from '../providers/OptimisticActionsContext';

export const useAutoPatternsOptimisticActions = (
  collectionId: string,
  collection: CollectionState<any>,
) => {
  const { registerOptimisticActions } = useOptimisticActionsContext();

  const optimisticActions = useOptimisticActions(collection, {
    orderBy: ({ sort }) => sort || [],
  });

  useEffect(() => {
    if (optimisticActions && collectionId) {
      registerOptimisticActions(collectionId, optimisticActions);
    }
  }, [optimisticActions, collectionId, registerOptimisticActions]);

  return optimisticActions;
};
